    .ic-content {
        margin: 45% auto;
        height: 282px;
        width: 64%;  
        background-color: white;
        border-radius: 20px;
        border-style: solid;
        border-color: #EBE7DD;
        border-width: 4px;
        text-align: center;
    }
    
    .ic-title {
        text-align: center;
        font-size: 1.5rem;
        color: #001348;
        margin-top: 28%;
    }
    
    .ic-bottom-left {
        position: absolute;
        bottom: 33%;
        left: 11%;
        height: 15.5%;
        width: 36%;
    }
    
    .ic-input-container {
        background-color: white;
        border-radius: 20px;
        border-style: solid;
        border-color: #D8D3BE;
        border-width: 1px;
        text-align: center;
        color: darkgrey;
        width: 65%;
        height: 20%; 
        margin: 20% auto;
    }
    
    .ic-input-box {
        border: 0;
        box-shadow: -8px 10px 0px -7px #ebebeb, 8px 10px 0px -7px #ebebeb;
        -webkit-transition: box-shadow 0.3s;
        transition: box-shadow 0.3s;
        font-size: 1rem;
        text-align: center;
        width: 60%;
        margin: 16% auto;
    }
    
    .ic-input-box:focus {
        outline: none;
        box-shadow: -8px 10px 0px -7px #4EA6EA, 8px 10px 0px -7px #4EA6EA;
    }
    
    .ic-button {
        text-align: center;
        color: white;
        background-color: #001348;
        height: 16%;
        width: 101%;
        border-radius: 23px;
        font-size: 1.5rem;
        margin-top: 2%;
        border-style: none;
    }
    
    .ic-content span {
        font-family: "Script MT Bold";
    }
    
    .msg-error{
        font-size: 1rem;
        font-family: Arial, Helvetica, sans-serif;
        color:red;
    }
  
  
