.home-centered {
    top: 50%;
    left: 50%;
    width:86%;
    height:28%;
    margin-top: -30%; /*set to a negative number 1/2 of your height*/
    margin-left: -44%; /*set to a negative number 1/2 of your width*/
    border: 1px solid #ccc;
    position:absolute;
  }
.home-title{
    background-color: white;
    border-radius: 60px;
    border-style: solid;
    border-color: whitesmoke;
    border-width: thick;
    text-align: center;
    font-family: 'Edwardian Script ITC';
    line-height: 115%;
    font-size: 3.5rem;
    color:darkgrey;
}
.home-bottom-left{
    position: absolute;
    bottom: -50px;
    left: -40px;
    height: 140px;
    width: 140px;
}
.home-top-right{
    position: absolute;
    top: -50px;
    right: -40px;
    height: 140px;
    width: 140px;
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
}
.home-rings{
    width: 30px;
    height: 30px;
}