#conf-content {
    height: 100vh;
    width: 100vw;
    background: #F4F3DF;
}
.conf-button-send{
    position: absolute;
    bottom: 10%;
    right: 10%;
    border-radius: 50%;
    background-color: white;
    height: 50px;
    width: 50px;
    -moz-box-shadow: 0 3px 5px #ccc;
    -webkit-box-shadow: 0 3px 5px #ccc;
    -o-box-shadow: 0 3px 5px #ccc;
    -ms-box-shadow: 0 3px 5px #ccc;
    box-shadow: 0 3px 5px #ccc;
    border: 1px solid #F6F3EB;
    font-size: 2rem;
    color: #71C2F0;
}
.conf-button-send:focus .conf-button-back:focus{
    outline: none;
}

.conf-button-back {
    border-radius: 50%;
    background-color: #EBE7DB;
    position: absolute;
    left: 2%;
    top: 1%;
    height: 50px;
    width: 50px;
    -moz-box-shadow: 0 3px 5px #ccc;
    -webkit-box-shadow: 0 3px 5px #ccc;
    -o-box-shadow: 0 3px 5px #ccc;
    -ms-box-shadow: 0 3px 5px #ccc;
    box-shadow: 0 3px 5px #ccc;
    border: 1px solid #F6F3EB;
    font-size: x-large;
}

.conf-invitation {
    width: 80%;
    height: 70%;
    background: white;
}

.conf-centered {
    top: 50%;
    left: 50%;
    margin-top: -40%;
    margin-left: -40%;
    position: absolute;
}

.conf-details {
    border: 2px solid #E4D5AD;
    background: transparent;
    width: 80%;
    height: 85%;
    border-radius: 3%;
}

.conf-bottom-left {
    position: absolute;
    bottom: -50px;
    left: -55px;
    height: 140px;
    width: 140px;
}

.conf-top-right {
    position: absolute;
    top: -50px;
    right: -55px;
    height: 140px;
    width: 140px;
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
}

.conf-title{
    font-family: "Script MT Bold"; 
    color:#253252;
}

.conf-paragraph{
    width: 65%;
    text-align: center;
    margin: 0 auto;
    font-family: "Monotype Corsiva Regular";
    color:#253252;
}

.conf-input-container {
    background-color: white;
    border-radius: 60px;
    border-style: solid;
    border-color: whitesmoke;
    border-width: thick;
    text-align: center;
    color: darkgrey;
    width: 75%;
    left: 11%;
    position: absolute;
    margin-top: 2%;
    height: 14%;
}

.conf-input-box {
    border: 0;
    box-shadow: -8px 10px 0px -7px #ebebeb, 8px 10px 0px -7px #ebebeb;
    -webkit-transition: box-shadow 0.3s;
    transition: box-shadow 0.3s;
    font-size: 1rem;
    text-align: center;
    width: 60%;
    margin-top: 7%;
}
.conf-input-box:focus {
    outline: none;
    box-shadow: -8px 10px 0px -7px #4EA6EA, 8px 10px 0px -7px #4EA6EA;
}
