.gl-header {
    background: white;
    color: #707070;
    position: fixed;
    top: 0;
    width: 100%;
    height: 110px;
    z-index: 10;
}

.gl-header div:not(#gl-gift) {
    text-align: left;
}

.gl-list-title {
    font-family: 'Lucida Calligraphy Italic';
    font-size: 15px;
    margin-top: 20px;
    margin-left: 75px;
}

.gl-gift-title {
    font-family: 'Edwardian Script ITC';
    font-size: 40px;
    margin: -15px auto;
    text-shadow: 0 3px 3px #d6d6d6;
    font-weight: bold;
}

.gl-content {
    margin: 182px auto;
    z-index: 5;
}

footer {
    display: flex;
    justify-content: center;
    padding: 5px;
    background-color: white;
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 51px;
}

.gl-confirm-button {
    border-radius: 15px;
    background: #465379;
    width: 99px;
    height: 30px;
    box-shadow: 0px 4px 3px #C4CFFC;
    color: white;
    margin: auto 18px auto auto;
    border: none;
}

.gl-gift {
    width: 322px;
    border: 1px solid #F6F3EB;
    border-radius: 24px;
    background: white;
    box-shadow: 0 3px 6px #d6d6d6;
    font-family: 'Lucida Calligraphy Italic';
    font-size: 20px;
    text-align: left;
    color: #707070;
    min-height: 91px;
    margin: 17px;
}

.gl-gift div {
    margin: 10% auto 0 14%;
}

.gl-gift p {
    width: 196px;
}

/* ------------------------------custom checkbox-------------------------- */

/* The container */

.gl-container {
    display: block;
    position: relative;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

/* Hide the browser's default checkbox */

.gl-container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom checkbox */

.checkmark {
    position: absolute;
    top: 0;
    left: 200px;
    height: 25px;
    width: 25px;
    background-color: #eee;
}

/* On mouse-over, add a grey background color */

.gl-container:hover input~.checkmark {
    background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */

.gl-container input:checked~.checkmark {
    background-color: #465379;
}

/* Create the checkmark/indicator (hidden when not checked) */

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */

.gl-container input:checked~.checkmark:after {
    display: block;
}

/* Style the checkmark/indicator */

.gl-container .checkmark:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

.gl-gift-checked {
    background: #F6F3EB;
}

/* ------------------------------Modal-------------------------- */

/* The Modal (background) */

.modal {
    display: block;
    /* Hidden by default */
    position: fixed;
    /* Stay in place */
    z-index: 1;
    /* Sit on top */
    left: 0;
    top: 0;
    width: 100%;
    /* Full width */
    height: 100%;
    /* Full height */
    overflow: auto;
    /* Enable scroll if needed */
    background-color: rgb(0, 0, 0);
    /* Fallback color */
    background-color: rgba(0, 0, 0, 0.4);
    /* Black w/ opacity */
}

/* Modal Content/Box */

.modal-content {
    background-color: #fefefe;
    margin: 40% auto;
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
    height: 55%;
    /* Could be more or less, depending on screen size */
}

.inner-border {
    width: 96%;
    height: 93%;
    border-radius: 10px;
    border: 2px solid #E4D5AD;
    margin: 10px auto;
}

.inner-border p {
    position: relative;
    top: -52px;
    font-family: 'Monotype Corsiva Regular';
    color:#545F93;
    text-align: center;
    font-size: 20px;
    width: 90%;
    margin: 0 auto;
}

/* The Close Button */

.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.close:hover, .close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}

.gl-bottom-left {
    position: relative;
    bottom: -270px;
    left: -67px;
    height: 98px;
    width: 111px;
}

.gl-top-right {
    position: relative;
    top: -40px;
    right: -67px;
    height: 98px;
    width: 111px;
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
}