#inv-content {
    height: 100vh;
    width: 100vw;
    background: white;
}

.inv-background {
    height: 570px;
    width: 90%;
    margin: 7.12px auto;
    position: relative;
    display: inline-block;
    background-image: url(../../fondo.png);
    border-radius: 10px;
    border: 1px solid #E3D4AD;
}

.inv-flower-bottom-left {
    left: -18px;
    bottom: -7px;
    position: absolute;
    width: 103px;
    height: 92px;
}

.inv-botton-left {
    background: #F3E9CB;
    /* fallback */
    background: linear-gradient(234deg, transparent 75px, #F3E9CB 0) top right;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: absolute;
    bottom: 0%;
    left: 0%;
    height: 127.28px;
    width: 93.35px;
    border-radius: 0 0 0 10px;
}

.inv-body {
    width: 90%;
    height: 87%;
    border-radius: 10px;
    box-shadow: 0px 1px #EBE3C4;
    background: white;
    margin: 5% auto;
    border: 2px solid #FAF9F5;
    padding-top: 34.5px;
}

.inv-line {
    background: #3B7FAD no-repeat scroll center;
    margin-left: 26px;
    width: 90px;
    height: 1px;
    box-shadow: 0pc 1px #000000;
}

.inv-parent-lines div {
    float: left;
    clear: none;
}

#inv-line-right {
    margin-right: 42px;
}

.inv-title {
    font-family: 'Edwardian Script ITC';
    color: #001348;
    font-size: 3.7rem;
    text-align: left;
    text-shadow: 0px 1px 3px #C6BDB9;
}

.inv-verbiage {
    font-family: 'Georgia Italic';
    color: #2C3870;
    font-size: 107%;
    text-align: center;
    width: 90%;
    height: 20%;
    margin: 14px auto;
}

.inv-date {
    width: 46%;
    height: 8%;
    border: 3px solid #E4D5AD;
    color: #2C3870;
    text-align: center;
    font-family: 'Franklin Gothic Demi Italic';
    margin: 18px auto;
    font-size: 1.1rem;
}

.inv-date div {
    margin: 8% auto;
}

.inv-dot {
    height: 3px;
    width: 3px;
    background-color: #2C3870;
    border-radius: 50%;
    display: inline-block;
    vertical-align: middle;
}

.inv-church {
    color: #2D475C;
    font-size: 1.3rem;
    margin: 18px auto 0;
    text-align: center;
    font-family: 'Georgia Italic';
    font-weight: bold;
}

.inv-hour {
    color: #2D475C;
    font-size: 1rem;
    font-family: sans-serif;
}

.inv-reception-title {
    font-family: 'Georgia Regular';
    text-decoration: underline;
    color: #2D475C;
    font-size: 107%;
    margin: 10px auto 0;
}

.inv-reception-place {
    color: #2D475C;
    font-size: 1.3rem;
    text-align: center;
    font-family: 'Georgia Italic';
    font-weight: bold;
}
.inv-biblical-quote{
    font-family: "Monotype Corsiva Regular";
    font-size: 1rem;
    color:#001348;
    margin: 19px auto 0;
    height: 19%;
    width: 60%;
}
.inv-biblical-ref{
    color:#001348;
    font-family: "Monotype Corsiva Regular";
    font-size: 0.8rem;
    margin: 19px auto 0;
    height: 5%;
    width: 25%;
    margin: 7px 62%;
}

.inv-button{
    background: #F8E5DD;
    border: 1px solid #F8E5DD;
    box-shadow: 0px 1px 3px #9e9d9d;
    height: 15%;
    width: 25%;
    font-weight: bold;
    border-radius: 10px;
    font-family: serif;
    color: #2D475C;
    margin: auto 5%;
} 
.hidden{
    display: none;
}